import { Component, Prop, h } from '@stencil/core';
import { Color, FontType, FontWeight } from '../../../globals/types/types';

@Component({
  tag: 'p-font',
  styleUrl: 'p-font.scss',
  shadow: true
})
export class PFont {

  @Prop() text: string = '';
  @Prop() type: FontType = 'info';
  @Prop() color: Color = 'black';
  @Prop() weight: FontWeight = 'default';
  @Prop() ellipsis: boolean = false;
  @Prop() blocked: boolean = false;

  render() {
    return (
      <p class={this.getClassList()} innerHTML={this.text}></p>
    );
  }

  getClassList() {
    return {
      ['text']: true,
      [`font-${this.type}`]: true,
      [`color-${this.color}`]: true,
      [`weight-${this.weight}`]: true,
      ['ellipsis']: this.ellipsis,
      ['blocked']: this.blocked
    }
  }
}
